import React from "react";
import "./Projects.css";
import Artwork from "./Artwork/Artwork";

function Projects() {
  return (
    <div className="text-center container container-fluid text-container">
      <div>
        <div className="title">
          <h2 className="projects-title animated slideInDown">
            Currrently working in
            <br />
            fantastic projects :)
          </h2>
        </div>
        <div className="working-gif animated fadeIn">
          <img
            className="working-gif-img "
            src="https://media.giphy.com/media/jt8mEC6Kffbwbv2tNH/giphy.gif"
            alt="wait"
          ></img>
          
        </div>
        <i class="fas fa-chevron-down animated bounce infinite"></i>
      </div>
      <Artwork />
    </div>
  );
}

export default Projects;
