import React from "react";
import "./HeadBar.css";

function HeadBar() {
  return (
    <nav className="navbar navbar-expand-lg sticky-top head-bar">
      <div className="container-fluid">
        <button
          class="navbar-toggler"
          type="button"
          data-mdb-toggle="collapse"
          data-mdb-target="#navbarNavAltMarkup"
          aria-controls="navbarNavAltMarkup"
          aria-expanded="false"
          aria-label="Toggle navigation"
        >
          <i class="fas fa-bars"></i>
        </button>
        <div className="collapse navbar-collapse" id="navbarNavAltMarkup">
          <div className="navbar-nav">
            <a className="nav-link active enlace" aria-current="#" href="#">Home</a>
            <a className="nav-link enlace" href="#/about">About</a>
            <a className="nav-link enlace" href="#/projects">Projects</a>
          </div>
        </div>
      </div>
    </nav>
  );
};

export default HeadBar;
