import React from "react";
import "./Description.css";

function Description() {
  return (
    <div className="row">
      <div className="col-md-6 animated fadeInLeft description">
        <h2>
          I'm Aarón, a{" "}
          <span className="iamfrontend">junior front-end developer</span> and an
          amateur illustrator, coding with the fresh breeze of{" "}
          <span className="iamfrontend">San Salvador</span>, El Salvador{" "}
          <span className="iamfrontend">❤</span>
        </h2>
        <div className="me-description">
          <p>
            I've learnt to program when I was 13 years old and I became
            interested in web development and freelancing at 16. My days are
            spent researching, learning new technologies, designing, and coding.{" "}
            {":)"}
          </p>
          <p>
            Out of the coding-life you'll find me drawing, painting, reading a
            book or writing an article.
          </p>
        </div>
      </div>
      <div className="col-md-6 .me-container text-center">
        <img
          className="animated fadeInRight me"
          src="https://i.imgur.com/uPghEPo.jpg"
          alt="me"
        ></img>
      </div>
    </div>
  );
}

export default Description;
